@mixin bg-retina($retina-img: "") {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("#{$prefix-css-img}#{$retina-img}");
  }
}

@mixin fix-ie-btn-text() {
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    & > span,
    &:active span,
    &:focus span {
      position: relative;
      top: 0;
      left: 0;
    }
  }
}

@mixin visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
